.app {
    min-height: 864px;
    background: url(../img/bg/bg_4.jpg) center center/cover no-repeat;
    padding: 48px 0 79px 0;

    .opportunity {
        width: 112px;
        height: 24px;
        background-color: #000000;
        color: #ffffff;
        font-family: "Roboto Thin";
        font-size: 11px;
        font-weight: 400;
        text-transform: uppercase;
        text-align: center;
        margin: 0 auto;
        padding-top: 5px;
        border-radius: 3px; }

    .app_header {
        color: #222222;
        font-family: 'Roboto Bold';
        font-size: 38px;
        font-weight: 700;
        text-transform: uppercase;
        text-align: center;
        margin-top: 38px;
        margin-bottom: 0px; }
    .app_subheader {
        color: #222222;
        font-family: 'Roboto Regular';
        font-size: 17px;
        font-weight: 400;
        text-align: center;
        margin-top: 19px;
        margin-bottom: 0px; }
    &_item {
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-top: 32px;
        padding: 91px 26px 35px 27px; }

    &_text {
        color: #ffffff;
        font-family: 'Roboto Regular';
        font-size: 24px;
        font-weight: 400;
        line-height: 31px;
        text-transform: uppercase;
        text-align: center; }
    &_btn {
        position: relative;
        width: 26px;
        height: 26px;
        background-color: #000000;
        display: block;
        margin: 0 auto;
        margin-top: 75px;
        border-radius: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0;
        &::before {
            content: "";
            display: block;
            width: 14px;
            height: 2px;
            background-color: $main-color;
            transition: 0.5s all; }
        &::after {
            position: absolute;
            left: 50%;
            transform: translate(-50%);
            content: "";
            display: block;
            width: 2px;
            height: 14px;
            background-color: $main-color;
            transition: 0.5s; }
        &:hover {
            &::before, &::after {
                background-color: $second-color; } } }


    &_item_1 {
        background: url(../img/slids/2-layers.jpg) center center/cover no-repeat; }
    &_item_2 {
        background: url(../img/slids/3-layers.png) center center/cover no-repeat; }
    &_item_3 {
        background: url(../img/slids/4-layers.png) center center/cover no-repeat; }
    &_item_4 {
        background: url(../img/slids/5-layers.png) center center/cover no-repeat;
        margin-top: 19px; }
    &_item_5 {
        margin-top: 19px;
        background: url(../img/slids/6-layers.png) center center/cover no-repeat; }
    &_item_6 {
        margin-top: 19px;
        background: url(../img/slids/6-layers.png) center center/cover no-repeat; } }

